import React from "react";
import s from './Post.module.css';

const Post = (props) => {
    return (

        <div className={s.item}>
            <div className={s.sip}><img alt ="" src='data:image/jpeg;base64,' />
            </div>
            {props.message}
            <div><span>{props.lcount} likes</span></div>
        </div>


        
    )
};
export default Post;